body {
  font-family: "Libre Baskerville";
  color: #1f1f1f;
  line-height: 130%;
}

body .App {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
nav {
  background-color: white;
  border-top: 2px solid #211f20;
  border-bottom: 1px solid #c9c9c9;
    width: 100%;
    margin: 10px 0 -10px 0;
    top: 0;
    text-align: center;
  }
  
  nav > ul {
    list-style: none;
    margin: 0 auto;
    max-width: 1170px;
    width: 100%;
    padding: 0;
  }
  
  nav > ul > li {
    height: 100%;
    font-size: 10.5pt;
    display: inline-block;
    transition: all 0.3s ease-in;
  }
  
  nav > ul > li > a {
    display: block;
    font-size: 16px;
    text-decoration: none;
    color: #1f1f1f;
    margin: 0 11px -2px 11px;
    padding: 15px 0 15px 0;
    border-bottom: 3px solid rgba(255,255,255,0);
  }

  nav > ul > li > a:hover {
    color: #ae0009;
    border-bottom: 3px solid #ae0009;
  }

  nav > ul > li > ul {
    position: absolute;
    background-color: #ae0009;
    list-style: none;
    border: 0;
    padding: 0;
    min-width: 180px;
    text-align: left;
  }

  nav > ul > li > ul > li {
    /*clear: left;*/
    float: left;
    width: 210px;
    
  }

  nav > ul > li > ul > li:nth-child(2n+1) {
    clear: left;
    border-right: 1px solid rgba(255,255,255,0.2);
  }

  nav > ul > li > ul > li a {
    display: block;
    padding: 13px 15px 13px 15px;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    color: #fff;
    text-decoration: none;
    font-size: 14px;
    line-height: 100%;
    white-space: nowrap;
  }

  nav > ul > li > ul > li a:hover {
    background-color: #800006;
  }
  
  .fixed-nav {
    position: fixed;
    margin-top: 0;
  }
  
  .content {
    width: 80%;
    margin: 0 auto;
    text-align: justify;
    padding-top: 20px;
  }
  
  .content > div {
    margin: 10px 0;
  }

  @media screen and (max-width:1000px) {
    .innerTitle {
      z-index: 1;
      background-color: #fff;
    }

    nav ul li {
      display: list-item;
      text-align: left;
    }

    nav ul li > a {
      font-size: 14px;
    }

    nav .has-children > a::after {
      content: ">";
      font-style: normal;
      font-weight: normal;
      float: right;
    }
    nav ul > li > a, nav ul > li > a:hover {
      background-color: #ae0009;
      padding: 15px 5px 15px 5px;
      border: 1px solid rgba(255,255,255,0.2);
      color: white;
      display: block;
      text-decoration: none;
      margin: 0 0 0 0;
    }
    nav {
      margin: 0 0 0 0;
      display: none;
      max-height: 100%;
      overflow: auto;
      box-sizing: border-box;
      position: fixed;
    }

    nav ul, nav ul > li > ul {
      display: block;
      position: static;
      background-color: #ae0009;
      list-style: none;
      border: 0;
      padding: 0;
      min-width: 180px;
      text-align: left;
    }
    nav ul > li, nav > ul > li > ul > li {
      float: none;
      clear: none;
      width: 100%;
    }

    nav ul > li > ul > li > a, nav ul > li > ul > li > a:hover {
      padding-left: 30px;
      font-size: 14px;
    }

    nav ul > li > ul > li > ul > li > a, nav ul > li > ul > li > ul > li > a:hover {
      padding-left: 50px;
      font-size: 14px;
    }
  }
div.flex, div.App > header {
    display: flex;
    flex: 1 1;
}

header {
    z-index: 10;
}

.hidden {
    display: none;
}

div.App > header {
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 5px 0 10px 0;
    width: 100%;
    background-color: #eeeeee;
}

.innerTitle {
    flex-direction: row;
    align-content: center;
    justify-content: center;
}

.headerTitle {
    position: relative;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 25px;
    margin-right: 25px;
    max-width: 1170px;
}

.headerTitle a {
    text-decoration: none;
    font-size: 13px;
    color: #1f1f1f;
}

.headerTitle a:hover {
    color: #ae0009;
}

.headerSocialNetworks {
    display: inline;
}

.headerSocialNetworks > a {
    font-style: normal;
    font-weight: normal;
    line-height: 1em;
    font-size: 20px;
    padding: 5px;
}

.headerLeftBlock, .headerLeftBlock a.fa {
    justify-content: center;
    flex-direction: column;
    color: #ae0009;
}

.headerLeftBlock > p {
    display: flex;
}

.headerCenterDiv {
    justify-content: center;
}

.headerRightBlock.desktop-only {
    justify-content: center;
    align-content: flex-start;
    flex-direction: column;
    align-items: flex-end;
}

div.mobile-only {
    display: none;
}

button.toggleResponsiveMenu {
    display: none;
}

@media screen and (max-width:1000px) {
    #logoImg {
        width: 196px;
    }
    div .desktop-only {
        display: none;
    }
    div .mobile-only {
        display: flex;
    }
    .headerRightBlock {
        justify-content: center;
        flex-direction: column;
        align-items: flex-end;
    }

    .headerRightBlock a{
        color: #ae0009;
    }

    .headerTitle a {
        font-size: 19pt;
    }

    div.App > header {
        position: fixed;
        border-bottom: 2px solid #ae0009;
        top: 0;
        width: 100%;
        background-color: #fff;
    }

    button.toggleResponsiveMenu {
        display: block;
        position: absolute;
        z-index: 99;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        outline: none;
        overflow: hidden !important;
        background: transparent;
        border: none !important;
        font-size:0;
        cursor: pointer;
    }
}
main {
    background-color: #fff;
    font-size: 11pt;
    color: #1f1f1f;
    padding: 30px 0 0 0;
}

div.banner {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    font-size: 9px;
    color: #666;
    text-transform: uppercase;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 11pt;
    width: 100%;
    overflow: hidden;
}

div.banner img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.MainNews {
    color: #1f1f1f;
    line-height: 150%;
    font-size: 14px;
    overflow: hidden;
    display: block;
    width: calc(100%);
    height: calc(100%); 
}

.MainNews p {
    text-align: justify;
}

.MainNews img {
    width: 100%;
}

.MainNews h2 {
    font-size: 25px;
    line-height: 130%;
    vertical-align: baseline;
    box-sizing: border-box;
    font-weight: inherit;
    margin-top: 10px;
    margin-bottom: 10px;
}

.MainNews a, .MainNews:visited {
    text-decoration: none;
    color: #1f1f1f;
}

.MainNews a:hover {
    font-weight: bold;
}
.highlights {
    display: flex;
    background-color: #f3efe3;
    border-top: 2px solid #211f20;
    width: 100%;
    height: 100%;
    font-size: 11pt;
    color: #1f1f1f;
    flex-direction: column;
}

.highlights h2 {
    padding: 4px 22px 13px 20px;
    line-height: 120%;
    font-weight: normal;
    font-style: normal;
    font-size: 14px;
    text-transform: uppercase;
    border-bottom: 1px solid #c9c9c9;
    display: flex;
}

.highlights > div {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1 1;
}

.highlightNewsItem {
    margin-left: 18px;
    margin-right: 18px;
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.highlightNewsItem h3 {
    font-size: 14px;
    line-height: 130%;
    display: flex;
    flex: 2 2;
    margin: 0;
}

.highlightNewsItem .imgDiv {
    flex: 1 1;
    display: flex;
}

.highlightNewsItem .imgDiv img {
    width: 100%;
    display: block;
    object-fit: contain;
}
