.highlights {
    display: flex;
    background-color: #f3efe3;
    border-top: 2px solid #211f20;
    width: 100%;
    height: 100%;
    font-size: 11pt;
    color: #1f1f1f;
    flex-direction: column;
}

.highlights h2 {
    padding: 4px 22px 13px 20px;
    line-height: 120%;
    font-weight: normal;
    font-style: normal;
    font-size: 14px;
    text-transform: uppercase;
    border-bottom: 1px solid #c9c9c9;
    display: flex;
}

.highlights > div {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1;
}

.highlightNewsItem {
    margin-left: 18px;
    margin-right: 18px;
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.highlightNewsItem h3 {
    font-size: 14px;
    line-height: 130%;
    display: flex;
    flex: 2 2;
    margin: 0;
}

.highlightNewsItem .imgDiv {
    flex: 1 1;
    display: flex;
}

.highlightNewsItem .imgDiv img {
    width: 100%;
    display: block;
    object-fit: contain;
}