nav {
  background-color: white;
  border-top: 2px solid #211f20;
  border-bottom: 1px solid #c9c9c9;
    width: 100%;
    margin: 10px 0 -10px 0;
    top: 0;
    text-align: center;
  }
  
  nav > ul {
    list-style: none;
    margin: 0 auto;
    max-width: 1170px;
    width: 100%;
    padding: 0;
  }
  
  nav > ul > li {
    height: 100%;
    font-size: 10.5pt;
    display: inline-block;
    transition: all 0.3s ease-in;
  }
  
  nav > ul > li > a {
    display: block;
    font-size: 16px;
    text-decoration: none;
    color: #1f1f1f;
    margin: 0 11px -2px 11px;
    padding: 15px 0 15px 0;
    border-bottom: 3px solid rgba(255,255,255,0);
  }

  nav > ul > li > a:hover {
    color: #ae0009;
    border-bottom: 3px solid #ae0009;
  }

  nav > ul > li > ul {
    position: absolute;
    background-color: #ae0009;
    list-style: none;
    border: 0;
    padding: 0;
    min-width: 180px;
    text-align: left;
  }

  nav > ul > li > ul > li {
    /*clear: left;*/
    float: left;
    width: 210px;
    
  }

  nav > ul > li > ul > li:nth-child(2n+1) {
    clear: left;
    border-right: 1px solid rgba(255,255,255,0.2);
  }

  nav > ul > li > ul > li a {
    display: block;
    padding: 13px 15px 13px 15px;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    color: #fff;
    text-decoration: none;
    font-size: 14px;
    line-height: 100%;
    white-space: nowrap;
  }

  nav > ul > li > ul > li a:hover {
    background-color: #800006;
  }
  
  .fixed-nav {
    position: fixed;
    margin-top: 0;
  }
  
  .content {
    width: 80%;
    margin: 0 auto;
    text-align: justify;
    padding-top: 20px;
  }
  
  .content > div {
    margin: 10px 0;
  }

  @media screen and (max-width:1000px) {
    .innerTitle {
      z-index: 1;
      background-color: #fff;
    }

    nav ul li {
      display: list-item;
      text-align: left;
    }

    nav ul li > a {
      font-size: 14px;
    }

    nav .has-children > a::after {
      content: ">";
      font-style: normal;
      font-weight: normal;
      float: right;
    }
    nav ul > li > a, nav ul > li > a:hover {
      background-color: #ae0009;
      padding: 15px 5px 15px 5px;
      border: 1px solid rgba(255,255,255,0.2);
      color: white;
      display: block;
      text-decoration: none;
      margin: 0 0 0 0;
    }
    nav {
      margin: 0 0 0 0;
      display: none;
      max-height: 100%;
      overflow: auto;
      box-sizing: border-box;
      position: fixed;
    }

    nav ul, nav ul > li > ul {
      display: block;
      position: static;
      background-color: #ae0009;
      list-style: none;
      border: 0;
      padding: 0;
      min-width: 180px;
      text-align: left;
    }
    nav ul > li, nav > ul > li > ul > li {
      float: none;
      clear: none;
      width: 100%;
    }

    nav ul > li > ul > li > a, nav ul > li > ul > li > a:hover {
      padding-left: 30px;
      font-size: 14px;
    }

    nav ul > li > ul > li > ul > li > a, nav ul > li > ul > li > ul > li > a:hover {
      padding-left: 50px;
      font-size: 14px;
    }
  }