.MainNews {
    color: #1f1f1f;
    line-height: 150%;
    font-size: 14px;
    overflow: hidden;
    display: block;
    width: calc(100%);
    height: calc(100%); 
}

.MainNews p {
    text-align: justify;
}

.MainNews img {
    width: 100%;
}

.MainNews h2 {
    font-size: 25px;
    line-height: 130%;
    vertical-align: baseline;
    box-sizing: border-box;
    font-weight: inherit;
    margin-top: 10px;
    margin-bottom: 10px;
}

.MainNews a, .MainNews:visited {
    text-decoration: none;
    color: #1f1f1f;
}

.MainNews a:hover {
    font-weight: bold;
}