main {
    background-color: #fff;
    font-size: 11pt;
    color: #1f1f1f;
    padding: 30px 0 0 0;
}

div.banner {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 9px;
    color: #666;
    text-transform: uppercase;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 11pt;
    width: 100%;
    overflow: hidden;
}

div.banner img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}