div.flex, div.App > header {
    display: flex;
    flex: 1;
}

header {
    z-index: 10;
}

.hidden {
    display: none;
}

div.App > header {
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 5px 0 10px 0;
    width: 100%;
    background-color: #eeeeee;
}

.innerTitle {
    flex-direction: row;
    align-content: center;
    justify-content: center;
}

.headerTitle {
    position: relative;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 25px;
    margin-right: 25px;
    max-width: 1170px;
}

.headerTitle a {
    text-decoration: none;
    font-size: 13px;
    color: #1f1f1f;
}

.headerTitle a:hover {
    color: #ae0009;
}

.headerSocialNetworks {
    display: inline;
}

.headerSocialNetworks > a {
    font-style: normal;
    font-weight: normal;
    line-height: 1em;
    font-size: 20px;
    padding: 5px;
}

.headerLeftBlock, .headerLeftBlock a.fa {
    justify-content: center;
    flex-direction: column;
    color: #ae0009;
}

.headerLeftBlock > p {
    display: flex;
}

.headerCenterDiv {
    justify-content: center;
}

.headerRightBlock.desktop-only {
    justify-content: center;
    align-content: flex-start;
    flex-direction: column;
    align-items: flex-end;
}

div.mobile-only {
    display: none;
}

button.toggleResponsiveMenu {
    display: none;
}

@media screen and (max-width:1000px) {
    #logoImg {
        width: 196px;
    }
    div .desktop-only {
        display: none;
    }
    div .mobile-only {
        display: flex;
    }
    .headerRightBlock {
        justify-content: center;
        flex-direction: column;
        align-items: flex-end;
    }

    .headerRightBlock a{
        color: #ae0009;
    }

    .headerTitle a {
        font-size: 19pt;
    }

    div.App > header {
        position: fixed;
        border-bottom: 2px solid #ae0009;
        top: 0;
        width: 100%;
        background-color: #fff;
    }

    button.toggleResponsiveMenu {
        display: block;
        position: absolute;
        z-index: 99;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        outline: none;
        overflow: hidden !important;
        background: transparent;
        border: none !important;
        font-size:0;
        cursor: pointer;
    }
}